<script>
import { ref } from 'vue';

export default {
  name: 'FractalTransactionPanel',
  props: {
    msg: String
  },
  setup() {
    const nftId = ref('')
    const sendAmount = ref(0.000015)
    return {
      nftId,
      sendAmount
    }
  },
  methods: {
    fractal_signMessage() {
      let params = {
        from: "",
        signStr: "Welcome to UniSat!\n\nClick to sign in and accept the UniSat Terms of Service (https://unisat.io/terms-of-service.html) and Privacy Policy (https://unisat.io/privacy-policy.html).\n    \nThis request will not trigger a blockchain transaction.\n    \nYour authentication status will reset after 24 hours.\n    \nWallet address:\nbc1p7pgnqe87red4cvd7ml6rh9pl9ufpr522k2y3dpeyrvfc6g2g3r3s3ae9dr\n    \nNonce:\n162d70e3-8c21-4cfd-9a22-9bf0a4b7be51\n"
      };
      this.$emit('generateTransactionParams', params, "fractal_signMessage")
    },

    fractal_send() {
      if (this.sendAmount == 0) {
        alert("不能为零")
        return
      }
      let params = {
        from: '',
        to: 'bc1p2s7fns5cn3scu8576h4fna8zwuugmfa9llx2cn8t3fpyfr2zz9ssnnftem', // pattern测试钱包的Taproot地址
        value: this.sendAmount
      }
      this.$emit('generateTransactionParams', params, "fractal_send")
    },

    fractal_signPsbts_up() {
      // 上架 
      /// Goose#58658
      alert("维护中，暂不可使用")
      return
      // let psbt1 = ""
      // this.$emit('generateTransactionParams', {
      //   psbtHexs: [psbt1],
      //   options: { autoFinalized: 0 }
      // }, "fractal_signPsbts")
    },

    fractal_sendInscription() {
      if (!this.nftId) {
        alert("请输入NFT ID")
        return
      }
      this.$emit('generateTransactionParams', {
        address: '19FSGZyDM3KmHpaCWwDrkfaz8nmTEBfwcR', // pattern测试钱包的legacy地址
        inscriptionId: this.nftId,
        options: {
          feeRate: 15
        }
      }, "fractal_sendInscription")
    }
  }
}
</script>

